<template>
  <v-parallax
    dark
    src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    height="800"
  >
    <v-layout
      align-center
      row
      justify-center
    >
      <v-flex xs12 sm12 md12>
        <h1 class="display-4 font-weight-bold mb-1">GoCheckIn</h1>
      </v-flex>
    </v-layout>
    <v-layout
      align-top
      row
      justify-center
    >

      <v-flex xs6 sm6 md6>
        <h1 class="heading">Scan Barcode to Login</h1>
        <v-dialog v-model="dialogQrcode" max-width="600px">
          <v-btn slot="activator" color="primary" fab large dark>
            <v-icon>blur_on</v-icon>
          </v-btn>

          <v-card>
            <v-card-title>
              <v-layout align-center row justify-center>
                <v-flex xs12 sm12 md12>
                  <v-icon>blur_on</v-icon>
                  <span class="headline">{{ formTitle }} </span>
                  <v-icon>blur_on</v-icon>
                </v-flex>
              </v-layout>              
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex
                    xs12
                    sm12
                    md12
                    d-flex
                  >
                  <qrcode-stream @decode="barcodeDecoded" :paused="qrPaused" :camera="{ facingMode: 'user' }">
                    <v-progress-circular
                      v-if="qrPaused"
                      :size="70"
                      :width="7"
                      color="primary"
                      indeterminate
                    ></v-progress-circular>
                  </qrcode-stream>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>

      <v-flex xs6 sm6 md6>
        <h1 class="heading">Numeric Pad to Login</h1>
        <v-dialog v-model="dialogDialpad" max-width="600px">
          <v-btn slot="activator" color="primary" fab large dark>
            <v-icon>dialpad</v-icon>
          </v-btn>

          <v-card>
            <v-card-title>
              <v-layout align-center row justify-center>
                <v-flex xs12 sm12 md12>
                  <v-icon>dialpad</v-icon>
                  <span class="headline">{{ formTitle }} </span>
                  <v-icon>dialpad</v-icon>
                </v-flex>
              </v-layout>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout row>
                  <v-flex xs12 sm12 md12 d-flex>
                    <v-text-field readonly type="text" label="Username" @focus="keyboardShow" v-model="username"></v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout row>
                  <v-flex>
                    <v-text-field readonly type="text" label="Password" @focus="keyboardShow" v-model="password"></v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout align-center row justify-center>
                  <v-flex>
                    <v-btn fab dark color="primary" v-if="!keyboard.visible" @click.native="dialpadLogin">
                      <v-icon large>touch_app</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>

                <v-layout row>
                  <v-flex xs12 sm12 md12 d-flex>
                    <vue-touch-keyboard v-if="keyboard.visible" :layout="keyboard.layout" :input="keyboard.input" :change="keyboardManualDispatch" :cancel="keyboardHide" :accept="keyboardAccept"/>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>  

    </v-layout>
  </v-parallax>
</template>

<script>
import VueTouchKeyboard from 'vue-touch-keyboard'
import style from 'vue-touch-keyboard/dist/vue-touch-keyboard.css'
import { NumericInput } from 'numeric-keyboard'
import { QrcodeStream } from 'vue-qrcode-reader'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog, Exceptions } from '@/utils'
import { components } from 'aws-amplify-vue'

// Vue.use(VueTouchKeyboard)

export default {
  data() {
    return {
      keyboard: {
        visible: false,
        layout: 'numeric',
        input: null,
        options: {
          useKbEvents: false,
          preventClickEvent: false
        },
      },


      dialogDialpad: false,
      dialogQrcode: false,
      qrPaused: false,
      username: null,
      password: null,
    }
  },
  created () {
    // this.qrPaused = false
  },
  computed: {
    formTitle () {
      return 'Scan your QR code to login!'
    },
  },
  methods: {
    async barcodeDecoded(decodedString) {
      
      let loginParams
      
      try {
        if (decodedString) {
          this.qrPaused = true
          loginParams = JSON.parse(decodedString)
        } else {
          this.dialogQrcode = false
          this.qrPaused = false
          return
        }        
      } catch (e) {
        window.alert('Please use correct barcode.')

        this.dialogQrcode = false
        this.qrPaused = false

        return this.$router.go()  
      }

      try {
        await this.customLogin(loginParams)

        this.dialogQrcode = false
        this.qrPaused = false
      } catch (e) {
        console.error(e)

        window.alert(e.message)

        this.dialogQrcode = false
        this.qrPaused = false

        return this.$router.go()        
      }

    },

    async dialpadLogin() {
      
      const loginParams = {username: this.username, password: this.password}

      try {
        await this.customLogin(loginParams)

        this.dialogDialpad = false
      } catch (e) {
        console.error(e)

        window.alert(e.message)

        this.dialogDialpad = false

        return this.$router.go()        
      }

    },

    keyboardAccept(text) {
      console.log("Input text: " + text)
      this.keyboardHide()
    },

    keyboardShow(e) {
      console.log('e', e)
      this.keyboard.input = e.target

      if (!this.keyboard.visible)
        this.keyboard.visible = true
    },

    keyboardHide() {
      this.keyboard.visible = false
    },
    keyboardManualDispatch () {
      this.keyboard.input.dispatchEvent(new Event('input', { bubbles: true }))
    },
    ...mapActions({
      customLogin: 'session/customLogin',
      logout: 'session/logout',
    })
  },
  components: {
    'qrcode-stream': QrcodeStream,
    'amplify-authenticator': components.Authenticator,
    "vue-touch-keyboard": VueTouchKeyboard.component
  }
}
</script>

